import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-img',
  templateUrl: './img.page.html',
  styleUrls: ['./img.page.scss'],
})
export class ImgPage implements OnInit {
  estado;numero;fondo;mostrar=false;
  arrImg=["https://firebasestorage.googleapis.com/v0/b/safeexam-473f1.appspot.com/o/bangPro%2FSOLIDO-1.png?alt=media&token=63adfbea-eb11-453e-ab07-df5709841fa1",
  "https://firebasestorage.googleapis.com/v0/b/safeexam-473f1.appspot.com/o/bangPro%2FSOLIDO-2.png?alt=media&token=ad788874-7d67-4bb3-ada2-8ec9aec31a94",
  "https://firebasestorage.googleapis.com/v0/b/safeexam-473f1.appspot.com/o/bangPro%2FGASEOSO-1.png?alt=media&token=f1217aab-6047-4316-8e15-bdec17466bb5",
  "https://firebasestorage.googleapis.com/v0/b/safeexam-473f1.appspot.com/o/bangPro%2FGASEOSO-2.png?alt=media&token=bea85076-594f-4d63-a7e0-f8c75bf0fc19",
  "https://firebasestorage.googleapis.com/v0/b/safeexam-473f1.appspot.com/o/bangPro%2FLIQUIDO-1.png?alt=media&token=0b951010-8353-48f8-861b-6565ed6919d3",
  "https://firebasestorage.googleapis.com/v0/b/safeexam-473f1.appspot.com/o/bangPro%2FLIQUIDO-2.png?alt=media&token=7c3ba6ca-25ef-4b0b-a232-61dfd4334385"]
  async ngAfterViewInit(){
    this.route.params.subscribe(params => { 
      const _numero= params.numero;
      this.numero = _numero;
      this.fondo=this.arrImg[this.numero];
    });
  }
  constructor(private route: ActivatedRoute,private router:Router) { }

  ngOnInit() {
    this.fondo=this.arrImg[this.numero];
  }
  cambiador(opcion){
    var pos:number=0;
    if(opcion==1){
       pos=parseInt(this.numero)+1;
    }
    else{
      pos= parseInt(this.numero);
    }
    this.fondo=this.arrImg[pos];
    this.mostrar= !this.mostrar
  }
  home(){
    this.router.navigateByUrl('home/explora');
  }
}
